.navbar {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  background: transparent;
  z-index: 9;
  min-height: 70px;
}
.navbar .navbar-nav li {
  position: relative;
  margin-left: 35px;
}
.navbar .navbar-nav li .nav-link {
  color: #a50dcfcb !important;
  font-size: 16px;
  text-transform: capitalize;
  transition: all 0.3s ease;
  font-weight: 500;
}
.navbar .navbar-nav li .nav-link:before {
  content: "";
  position: absolute;
  left: 0px;
  bottom: 0;
  background: #a60dcf;
  height: 2px;
  width: 0;
  transition: 0.3s;
}
.navbar .navbar-nav li .nav-link:hover:before {
  width: 100%;
}
.navbar .navbar-nav .active a:before {
  width: 100%;
}

.navbar-bg {
  background: #fff !important;
  box-shadow: 0px 10px 40px rgba(0, 0, 0, 0.05);
  padding: 0;
  position: fixed;
  top: -100px;
  left: 0;
  width: 100%;
  transform: translateY(100px);
  transition: transform 0.5s ease;
}

body {
  height: 5000px;
  background-color: #F4FEFF;
}

.btns {
  position: relative;
  padding: 12px 14px;
  text-transform: uppercase;
  color: #450b78;
  letter-spacing: 2px;
  font-size: 16px !important;
  margin-right: 18px;
}
.btns:hover {
  text-decoration: none;
  color: #FFF !important;
}
.btns:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #450b78;
  z-index: -1;
  transition: transform 0.5s;
  transform-origin: bottom right;
  transform: scale(0);
}
.btns:hover:before {
  transition: transform 0.5s;
  transform-origin: top left;
  transform: scale(1);
}
.btns:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: transparent;
  border: 1px solid #450b78;
  z-index: -1;
  box-sizing: border-box;
  transition: transform 0.5s;
  transform-origin: top left;
  transform: scale(1);
}
.btns:hover:after {
  transition: transform 0.5s;
  transform-origin: bottom right;
  transform: scale(0);
}

.settings-box {
  position: fixed;
  left: 0;
  top: 0;
  width: 250px;
  height: 100%;
  background-color: #fff;
  z-index: 1000;
  padding: 20px;
  transition: 0.3s;
}
.settings-box .option-title {
  font-weight: bold;
  color: #666;
  margin: 10px 0 15px;
  font-size: 18px;
}
.settings-box .toggle-settings {
  position: absolute;
  left: 250px;
  top: 100px;
  padding: 10px 15px;
  font-size: 22px;
  border-radius: 0 5px 5px 0;
  border: 1px solid #450b78;
  cursor: pointer;
  color: #450b78;
}
.settings-box.hide-settings {
  left: -250px;
}
.settings-box .option-box {
  margin-bottom: 15px;
  border-bottom: 1px solid #eee;
}
.settings-box .color-options ul li:nth-child(1) {
  background-color: #27ae60;
}
.settings-box .color-options ul li:nth-child(2) {
  background-color: #2980b9;
}
.settings-box .color-options ul li:nth-child(3) {
  background-color: #c0392b;
}
.settings-box .color-options ul li:nth-child(4) {
  background-color: #d35400;
}
.settings-box .color-options ul li:nth-child(5) {
  background-color: #8e44ad;
}
.settings-box .color-options ul li {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  opacity: 0.7;
  cursor: pointer;
  display: inline-block;
}
.settings-box .color-options ul li.active {
  opacity: 1;
}

