$themes: (
    green-theme: (
        color: #27ae60
    ),
    blue-theme: (
        color: #2980b9
    ),
    red-theme: (
        color: #c0392b
    ),
    orange-theme: (
        color: #d35400
    ),
    purple-theme: (
        color: #8e44ad
    )
);

@each $theme,$map in $themes {
    .#{$theme} {
        $color: map-get($map, color);
        header .header-content{
            h1{
                color: $color;
            }
            h6{
                color: $color;
            }
        }
        .btns{
            color: $color;
            &:before{
                background-color: $color;
            }
            &:after{
                border: 1px solid $color;
            }
        }
        .features.block{
            .features-content{
                .feature-title{
                    color: $color;
                }
            }
            .single-features{
                .feature-icon{
                    span{
                        background-image: linear-gradient(90deg, $color 0%, #926ef8 100%);
                    }
                }
            }
        }
        .services{
            .section-title{
                h2{
                    color: $color;
                }
            }
            .active{
                &:hover{
                    border: 1px solid $color;
                }
            }
            .services-caption{
                &:hover{
                    border: 1px solid #b8a2ff;
                }
                .service-cap{
                    h4{
                        a{
                            color: $color;
                        }
                    }
                }
            }
        }
        .applic-apps{
            .single-cases-info{
                h3{
                    color: $color;
                }
            }
        }
        .pricing-card{
            .single-card{
                span{
                    color: $color;
                }
            }
        }
        .clients{
            .section-title{
                h2{
                    color: $color;
                }
            }
            .customar-active{
                .single-customer{
                    .what-cap{
                        h4{
                            a{
                                color: $color;
                            }
                        }
                    }
                }
            }
        }
    }
}