.navbar{
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    background: transparent;
    z-index: 9;
    min-height: 70px;
    .navbar-nav{
        li{
            position: relative;
            margin-left: 35px;
            .nav-link{
                color: #a50dcfcb !important;
                font-size: 16px;
                text-transform: capitalize;
                transition: all .3s ease;
                font-weight: 500;
                &:before{
                    content: "";
                    position: absolute;
                    left: 0px;
                    bottom: 0;
                    background: #a60dcf;
                    height: 2px;
                    width: 0;
                    transition: .3s;
                }
                &:hover:before{
                    width: 100%;
                }
            }
        }
        .active{
            a{
                &:before{
                    width: 100%;
                }
            }
        }
    }
}
.navbar-bg{
    background: #fff !important;
    box-shadow: 0px 10px 40px rgba(0, 0, 0, 0.05);
    padding: 0;
    position: fixed;
    top: -100px;
    left: 0;
    width: 100%;
    transform: translateY(100px);
    transition: transform .5s ease;
}