header{
    padding-top: 125px;
    .header-content{
        margin-bottom: 30px;
        h6{
            font-size: 16px;
            text-transform: uppercase;
            color: $mainColor;
        }
        h1{
            width: 80%;
            font-size: 52px;
            font-weight: bolder;
            @media (max-width: 992px) {
                font-size: 40px;
                width: 90%;
            }
        }
        p{
            margin-top: 28px;
            margin-bottom: 38px;
            width: 60%;
            color: $textColor;
        }
    }
}

.features{
    background-color: #fff;
    padding-bottom: 50px;
    img{
        max-width: 100%;
    }
    .features-content{
        .feature-title{
            font-size: 50px;
            font-weight: 700;
            line-height: 1.2;
            margin-bottom: 90px;
            width: 90%;
        }
        .single-features{
            margin-bottom: 60px;
            .feature-icon{
                span{
                    float: left;
                    padding: 20px;
                    color: white;
                    border-radius: 50px;
                    font-size: 22px;
                }
            }
            .features-caption{
                margin-left: 80px;
                p{
                    color: $textColor;
                }
            }
        }
    }
}

.services{
    padding-top: 100px;
    padding-bottom: 100px;
    .section-title{
        h2{
            font-size: 50px;
            font-weight: 600;
            line-height: 1.2;
            margin-bottom: 90px;
            color: $mainColor;
        }
    }
    .services-caption{
        background: #fff;
        padding: 84px 45px 56px;
        border: 1px solid transparent;
        border-radius: 7px;
        transition: .5s;
        .service-icon{
            display: inline-block;
            position: relative;
            span{
                position: relative;
                color: #fff;
                font-size: 40px;
                height: 105px;
                width: 105px;
                display: block;
                line-height: 105px;
                border-radius: 50%;
                text-align: center;
                background: #e88cea;
                margin-bottom: 42px;
                transition: .5s;
            }
            &:before{
                content: "";
                background-image: url(../img/shape/dot-bg.png);
                width: 80px;
                height: 135px;
                position: absolute;
                right: -29px;
                top: -35px;
                z-index: 0;
                transition: .5s;
            }
            &:hover:before{
                right: -40px;
            }
        }
        .service-cap{
            h4{
                a{
                    font-size: 24px;
                    font-weight: 700;
                    line-height: 1.2;
                    margin-bottom: 27px;
                    display: inline-block;
                    text-decoration: none;
                }
            }
            P{
                font-family: "Poppins",sans-serif;
                color: $textColor;
                font-size: 16px;
                line-height: 30px;
                margin-bottom: 15px;
                font-weight: normal;
            }
        }
    }
    .active{
        border: 1px solid #b8a2ff;
    }
    
}

.applic-apps{
    background-color: #fff;
    padding-top: 100px;
    padding-bottom: 100px;
    position: relative;
    overflow: hidden;
    z-index: 0;
    .single-cases-info{
        margin-left: 80px;
        h3{
            font-size: 40px;
            font-weight: 700;
            line-height: 1.3;
            margin-bottom: 20px;
            padding-right: 30px;
            text-transform: capitalize;
        }
        p{
            color: $textColor;
            @media (max-width: 992px) {
                width: 70%;
            }
        }
    }
    .owl-carousel{
        overflow: hidden;
        position: relative;
        right: -15%;
        @media (max-width: 992px) {
            right: 0;
        }
        .owl-item {
            img {
                display: block;
                width: 100%;
            }
        }
        
    }
}

.best-pricing{
    background-image: url("../img/gallery/best_pricingbg.jpg");
    padding-top: 110px;
    padding-bottom: 280px;
    background-size: cover;
    background-repeat: no-repeat;
    position: relative;
    &:before{
        content: "";
        background-image: linear-gradient(90deg, #f9218d 0%, #6c19f6 100%);
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        opacity: 0.95;
    }
    .section-title{
        h2{
            color: #fff;
            font-size: 50px;
            font-weight: 700;
            line-height: 1.2;
            margin-bottom: 90px;
            @media (max-width: 992px) {
                font-size: 30px;
            }
        }
    }
}

.pricing-card{
    margin-top: -295px;
    .single-card{
        background-color: #fff;
        padding: 40px 25px;
        border-radius: 10px;
        box-shadow: 0px 0px 9px 0px rgba(131,93,248,0.07);
        transition: .4s;
        h4{
            color: $textColor;
        }
        .price-num{
            font-size: 40px;
            font-weight: bold;
            letter-spacing: 1.5px;
        }
        .price-month{
            font-size: 20px;
            font-weight: 500;
        }
        ul{
            margin: 40px 0;
            li{
                font-size: 17px;
                margin-top: 12px;
                color: $textColor;
            }
        }
    }
}

.clients{
    background-color: #fff;
    padding-top: 70px;
    padding-bottom: 100px;
    margin-top: 50px;
    .section-title{
        h2{
            font-size: 50px;
            @media (max-width: 992px) {
                font-size: 30px;
            }
        }
    }
    .customar-active{
        margin-top: 80px;
        .single-customer{
            .what-img{
                img{
                    max-width: 60px;
                }
            }
            .what-cap{
                h4{
                    margin-top: 30px;
                }
                p{
                    color: $textColor
                }
            }
        }
    }
}

.download{
    background-image: linear-gradient(90deg, #6c19f6 0%, #f9218d 100%);
    position: relative;
    padding-top: 120px;
    padding-bottom: 120px;
    .download-content{
        color: #fff;
        margin-bottom: 40px;
        h2{
            font-size: 50px;
            width: 90%;
            margin-bottom: 24px;
            @media (max-width: 991px) {
                font-size: 36px;
            }
        }
        p{
            width: 80%;
        }
        .app-btn{
            margin-top: 24px;
            .app-btn1{
                margin-right: 14px;
            }
        }
    }
    .app-img{
        img{
            width: 100%;
        }
    }
    .app-shape{
        .app-shape-top{
            position: absolute;
            top: 60px;
            left: 125px;
        }
        .app-shape-left{
            position: absolute;
            bottom: 0;
            left: 0;
        }
        .heartbeat{
            animation: heartbeat 1s infinite alternate;
        }
    }
}

.say-hello{
    position: relative;
    padding-top: 80px;
    padding-bottom: 80px;
    border-bottom: 1px solid #ffc9fd;
    overflow: hidden;
    .say-something-cap{
        h2{
            font-size: 45px;
            @media (max-width: 991px) {
                font-size: 36px;
                margin-bottom: 30px;
            }
        }
    }
    .say-btn{
        a{
            padding: 16px 26px;
        }
    }
    .say-shape{
        .say-shape1{
            position: absolute;
            left: -134px;
            bottom: -215px;
            animation: rotateme 40s infinite linear;
        }
    }
}

footer{
    padding-top: 120px;
    padding-bottom: 40px;
    background-color: #fff;
    // margin-top: 50px;
    .footer-logo{
        p{
            margin-top: 26px;
            color: $textColor;
        }
    }
    h5{
        margin-bottom: 28px;
    }
    ul{
        li{
            margin-bottom: 10px;
            a{
                color: $textColor !important;
            }
        }
    }
    p{
        color: $textColor;
    }
    form{
        input{
            display: inline-block !important;
            width: 80% !important;
        }
        button{
            float: right;
            width: 50px;
            height: 38px;
            background: #6c15b0;
            color: #fff;
            border: none;
        }
    }
}

@keyframes heartbeat{
    from{
        width: 30px;
    }
    to{
        width: 50px;
    }
}

@keyframes rotateme{
    from{
        transform: rotate(0);
    }
    to{
        transform: rotate(360deg);
    }
}