.btns{
    position: relative;
    padding: 12px 14px;
    text-transform: uppercase;
    letter-spacing: 2px;
    font-size: 16px !important;
    margin-right: 18px;
    &:hover{
        text-decoration: none;
        color: #FFF !important;
    }
    &:before{
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: -1;
        transition: transform 0.5s;
        transform-origin: bottom right;
        transform: scale(0);
    }
    &:hover:before{
        transition: transform 0.5s;
        transform-origin: top left;
        transform: scale(1);
    }
    &:after{
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: transparent;
        z-index: -1;
        box-sizing: border-box;
        transition: transform 0.5s;
        transform-origin: top left;
        transform: scale(1);
    }
    &:hover:after{
        transition: transform 0.5s;
        transform-origin: bottom right;
        transform: scale(0);
    }
}