.scroll-top {
    i{
        color: #fff;
        position: fixed;
        bottom: 50px;
        right: 50px;
        background: $mainColor;
        width: 40px;
        height: 40px;
        line-height: 40px;
        text-align: center;
        border-radius: 50%;
        opacity: 0;
    }
}